// =====VARS=========
$border: 15px solid #fff;
$tomato-red: #cf0604;
$bg-blue: #0c0931;
$tomatoish-red: #c90303;
$logo_ff: "Slackey", cursive;
$main_ff: "Major Mono Display", monospace;
// "Bowlby One SC", cursive;
// $main_ff: "Sarpanch", sans-serif;
// "Roboto Mono", monospace;
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: $main_ff;
  font-weight: 100;
  color: #fff;
}

// =========HELPERS, COMMON ELEMS=========
.visible {
  display: block;
}
