.btn {
  padding: 5px 10px;
  background-color: #656273;
  margin-bottom: 5px;
  border-radius: 5px;
  font-size: 1rem;
  &:last-child {
    margin-bottom: 0;
  }
}

.btn {
  outline: none;
  width: 6rem;
}
// =====APP TOP LVL====
#root,
body {
  background-color: $bg_blue;
  height: 100vh;
  width: 100%;
  min-width: 360px;
  .title {
    font-family: $logo_ff;
    color: $tomato_red;
    text-shadow: 1px 0px 0px gold, 1px 2px 0px #222;
    padding-left: 3.4rem;
  }
}

// =========TIMER===========
.timer {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  font-family: monospace;
  .timer__info {
    #timer-label {
      font-size: 0.8rem;
    }
    #time-left {
      font-size: 3.4rem;
      line-height: 1;
    }
  }
  .timer__buttons {
    margin-left: 1rem;
    justify-items: center;
    display: flex;
    flex-direction: column;
  }
}
// ========SETTINGS=====
.settings {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
.settings_header {
  flex: 1 100%;
  text-align: center;
  margin-top: 4rem;
  h3 {
    display: inline;
    padding: 5px 65px;
    font-size: 1.5rem;
    font-weight: normal;
    background-image: linear-gradient(
      to right,
      transparent,
      #656273,
      rgba(200, 200, 200, 0.5),
      transparent
    );
  }
}
.session,
.break {
  display: flex;
  flex-direction: column;
  text-align: center;
  // align-items: center;
  &:last-child {
    margin-left: 1rem;
  }
  button {
    outline: none;
  }
  .settings-wrap {
    display: flex;
  }
}
#session-label,
#break-label {
  // margin:  1rem 0;
  font-size: 1.2rem;
  margin-top: 1rem;
  line-height: 3;
}
#session-length,
#break-length {
  font-size: 2rem;
  padding: 0 0.3rem;
}

#break-increment,
#session-increment,
#break-decrement,
#session-decrement {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  background-color: rgba(200, 200, 200, 0.3);
  font-weight: bold;
  font-size: 1.3rem;
  line-height: 1.3;
  margin: 0px 10px;
}

