//media queries

@media screen and (max-width: 360px) {
  // body {
  //   font-size: 14px;
  // }
  .settings {
    flex-direction: column;
    text-align: center;
    &:last-child {
      margin-right: 0;
    }
    .settings_header {
      margin-bottom: 15px;
    }
    .break,
    .session {
      margin-top: 15px;
      align-self: center;
    }
  }
}
@media screen and (max-height: 320px) {
  body :not(.title) {
    font-size: 14px;
  }
  .settings_header {
    margin-top: 2rem;
  }
}
